<template>
    <div class="pagination">
        <ul>
            <li class="jumpBtn" v-if="showPreBtn" @click="prepage">上一页</li>

            <li v-for="page in pages" :class="{ active: page === parseInt(thisPage) }" @click="choosePage(page)">{{ page }}</li>

            <li class="jumpBtn" v-if="showNextBtn" @click="nextpage">下一页</li>
            <li class="jumpBtn" v-if="showLastBtn" @click="lastpage">末页</li>
            <li class="jumpBtn count">共 {{ count }} 页</li>
        </ul>
    </div>
</template>

<script>
export default {
    name: 'pagination',
    props: {
        thisPage: {
            type: String,
            required: true
        },
        count: {
            type: String,
            required: true
        }
    },
    data () {
        return {
            pages: [],
            showPreBtn: true,
            showNextBtn: true,
            showLastBtn: true
        }
    },
    computed: {
        pages () {
            return Array.from(new Array(parseInt(this.count)), (_, idx) => idx + 1)
        },
        showPreBtn () {
            return !(parseInt(this.thisPage) === 1 || parseInt(this.count) === 1)
        },
        showNextBtn () {
            return !(parseInt(this.thisPage) === parseInt(this.count) || parseInt(this.count) === 1)
        },
        showLastBtn () {
            return !(parseInt(this.thisPage) === parseInt(this.count) || parseInt(this.count) === 1)
        }
    },
    methods: {
        choosePage (page) {
            this.thisPage = page.toString()
        },
        prepage () {
            let thisPage = parseInt(this.thisPage)
            thisPage--
            this.thisPage = thisPage.toString()
        },
        nextpage () {
            let thisPage = parseInt(this.thisPage)
            thisPage++
            this.thisPage = thisPage.toString()
        },
        lastpage () {
            this.thisPage = this.count
        }
    }
}
</script>

<style lang="less" scoped>
.pagination {
    width: 100%;
    text-align: center;

    ul {
        line-height: 80px;
        display: block;

        li {
            margin: 2px;
            width: 30px;
            height: 30px;
            color: #777;
            line-height: 30px;
            user-select: none;
            cursor: pointer;
            border-radius: 3px;
            transition: all 300ms ease;
            display: inline-block;

            &:hover {
                color: #FFF;
                background: #F45757;
            }
        }

        .active {
            color: #FFF;
            background: #F45757;
        }

        .jumpBtn {
            width: auto;
            padding: 0 8px;
        }

        .count {
            user-select: auto;
            cursor: auto;

            &:hover {
                color: #777;
                background: none;
            }
        }
    }
}
</style>


