<template>
    <div class="mainBodyContainer">
        <notification :warning="notifa.warning" :success="notifa.success" :text="notifa.text"></notification>
        <div class="searchBar">
            <input type="text" placeholder="校园卡号、学号、失主姓名、归还者姓名" v-model="search">
            <button :class="{ able: sBtnAble }" @click="searchNow">搜索</button>
        </div>
        <div class="listHead">
            <div class="num">序号</div>
            <div class="rank">学号</div>
            <div class="username">失主</div>
            <div class="username">归还者</div>
            <div class="tools">操作</div>
        </div>
        <div class="listItem" v-for="item in cards">
            <div class="num">{{ item.id }}</div>
            <div class="rank">{{ item.studentNo }}</div>
            <div class="username">{{ item.studentName }}</div>
            <div class="username">{{ item.getName }}</div>
            <div class="tools">
                <i class="fa fa-check-circle" aria-hidden="true" @click="replyNow(item.id)" v-if="!parseInt(item.isReturn)"> 确认归还</i>
                <i class="fa fa-trash-o" @click="deleteCard(item.id)" v-if="user.rank >= 5"> 删除</i>
            </div>
        </div>
        <pagination :this-page.sync="pagination.thisPage" :count.sync="pagination.count"></pagination>
    </div>
    <div class="editMask" v-show="replyOpen" transition="fade">
        <div class="edit">
            <div class="editTitle">
                确认归还
                <span @click="closeReply"><i class="fa fa-times-circle"></i></span>
            </div>
            <div class="editBody">
                <div class="item">
                    <div class="left">学号</div>
                    <div class="right">{{ replyFor.studentNo }}</div>
                </div>
                <div class="item">
                    <div class="left">校园卡号</div>
                    <div class="right">{{ replyFor.icAccount }}</div>
                </div>
                <div class="item">
                    <div class="left">姓名</div>
                    <div class="right">{{ replyFor.studentName }}</div>
                </div>
                <div class="item">
                    <div class="left">归还者</div>
                    <div class="right">{{ replyFor.getName }}</div>
                </div>
                <div class="item">
                    <div class="left">备注信息</div>
                    <div class="right">{{ replyFor.remark }}</div>
                </div>
                <div class="item">
                    <div class="replyBtn">
                        <button class="able" @click="pushReply">确认归还</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import notification from '../../../components/notification.vue'
import pagination from '../../../components/pagination.vue'
import store from '../../../vuex/store'

export default {
    data () {
        return {
            cards: [{
                id: '1',
                studentNo: '2014130355',
                studentName: '陈俊毅',
                getName: '朱志远',
                isReturn: '1'
            }, {
                id: '2',
                studentNo: '2014130345',
                studentName: '朱志远',
                getName: '陈俊毅',
                isReturn: '0'
            }],
            replyFor: {
                id: '1',
                studentNo: '2014130355',
                studentName: '陈俊毅',
                icAccount: '135756',
                getName: '朱志远',
                remark: '1460638900'
            },
            reply: {
                id: 0
            },
            replyOpen: false,
            sbtnAble: false,
            showwaring: false,
            notifa: {
                warning: false,
                success: false,
                text: ''
            },
            search: '',
            user: store.state.user,
            pagination: {
                thisPage: '1',
                count: '1'
            }
        }
    },
    store,
    computed: {
        'sBtnAble': function () {
            return (this.search !== '')
        },
        user () {
            return store.state.user
        }
    },
    components: {
        notification,
        pagination
    },
    methods: {
        replyNow (id) {
            if (window.location.hostname === 'localhost') {
                this.replyOpen = true
            } else {
                this.$http.get('http://szdx.sinaapp.com/dashboard/rest/v1/card/card.php?action=getone&id=' + id).then(res => {
                    this.replyFor = res.data
                    this.reply.id = this.replyFor.id
                    this.replyOpen = true
                })
            }
        },
        closeReply () {
            this.replyOpen = false
        },
        pushReply () {
            this.$http.get('http://szdx.sinaapp.com/dashboard/rest/v1/card/card.php?action=return&id=' + this.reply.id).then(response => {
                if (response.data === 'success') {
                    this.notifa.text = '校园卡归还成功！'
                    this.notifa.success = true
                    this.closeReply()
                    this.$http.get('http://szdx.sinaapp.com/dashboard/rest/v1/card/card.php?action=list&page=' + this.pagination.thisPage).then(res => {
                        this.cards = res.data
                    })
                    setTimeout(() => {
                        this.notifa.success = false
                    }, 2000)
                } else {
                    this.notifa.text = '服务器故障！'
                    this.notifa.warning = true
                    setTimeout(() => {
                        this.notifa.warning = false
                    }, 2000)
                }
            }, () => {
                this.notifa.text = '服务器故障！'
                this.notifa.warning = true
                setTimeout(() => {
                    this.notifa.warning = false
                }, 2000)
            })
        },
        deleteCard (id) {
            this.$http.post('http://szdx.sinaapp.com/dashboard/rest/v1/card/card.php?action=delete&id=' + id)
                .then(response => {
                    if (response.data === 'success') {
                        this.notifa.text = '删除丢失信息成功！'
                        this.notifa.success = true
                        this.$http.get('http://szdx.sinaapp.com/dashboard/rest/v1/card/card.php?action=list&page=' + this.pagination.thisPage).then(res => {
                            this.cards = res.data
                        })
                        setTimeout(() => {
                            this.notifa.success = false
                        }, 2000)
                    } else {
                        this.notifa.text = '服务器故障！'
                        this.notifa.warning = true
                        setTimeout(() => {
                            this.notifa.warning = false
                        }, 2000)
                    }
                }, () => {
                    this.notifa.text = '服务器故障！'
                    this.notifa.warning = true
                    setTimeout(() => {
                        this.notifa.warning = false
                    }, 2000)
                })
        },
        searchNow () {
            if (!this.sBtnAble) return
            this.$http.get('http://szdx.sinaapp.com/dashboard/rest/v1/card/card.php?action=search&keyword=' + this.search)
                    .then(response => {
                        if (response.data.length !== 0) {
                            this.cards = response.data
                        } else {
                            this.notifa.text = '没有找到相应的结果'
                            this.notifa.warning = true
                            setTimeout(() => {
                                this.notifa.warning = false
                            }, 2000)
                        }
                    }, () => {
                        this.notifa.text = '服务器故障'
                        this.notifa.warning = true
                        setTimeout(() => {
                            this.notifa.warning = false
                        }, 2000)
                    })
        },
        format (m) {
            return m < 10 ? '0' + m : m
        },
        getPage () {
            this.$http.get('http://szdx.sinaapp.com/dashboard/rest/v1/card/card.php?action=getpage').then(res => {
                this.pagination.count = res.data
            })
        }
    },
    ready () {
        if (window.location.hostname !== 'localhost') {
            this.getPage()
            this.$http.get('http://szdx.sinaapp.com/dashboard/rest/v1/card/card.php?action=list&page=' + this.pagination.thisPage).then(res => {
                this.cards = res.data
            })
        }
    },
    filters: {
        formatTime (time) {
            time = parseInt(time) * 1000
            let date = new Date(time)
            let y = date.getFullYear()
            let m = date.getMonth() + 1
            let d = date.getDate()
            let h = date.getHours()
            let mm = date.getMinutes()
            return y + '-' + this.format(m) + '-' + this.format(d) + ' ' + this.format(h) + ':' + this.format(mm)
        }
    }
}
</script>

<style lang="less" scoped>
