    float: left;
    width: 85%;
    height: 90%;
    padding: 20px 40px 40px 10px;
    overflow: auto;

    .searchBar {
        float: left;
        width: 100%;
        margin-bottom: 10px;
        text-align: right;

        input {
            width: 200px;
            padding: 5px 10px;
