        div {
            float: left;
            text-align: center;
            border-right: 1px solid #FFF;
        }

        .num {
            width: 8%;
        }

        .username {
            width: 27%;
        }

        .rank {
            width: 18%;
        }

        .tools {
            width: 20%;
        }

    }

    .listItem {
        float: left;
        width: 100%;
        margin-top: 2px;
        font-size: 14px;
        line-height: 40px;
        user-select: none;
        cursor: pointer;
        
        div {
            float: left;
            text-align: center;
            border-right: 1px solid #FFF;
            border-radius: 3px;
        }

        .num {
            width: 8%;
        }

        .username {
            padding: 0 5px;
            width: 27%;
            display: block;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }

        .rank {
            width: 18%;
        }

        .tools {
            width: 20%;

            i:hover {
                color: #F45757;
            }

            i:nth-child(1) {
                margin-right: 5px;
            }

        }

        &:hover {
            background: #F9F9F9;
        }

    }

}

.editMask {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, .8);
    z-index: 1005;

    .edit {
        position: fixed;
        top: 50%;
