        overflow: auto;
        background: #FFF;
        border-radius: 5px;
        box-shadow: 0 0 20px rgba(0, 0, 0, .4);

        .editTitle {
            padding: 0 10px;
            color: #FFF;
            font-size: 16px;
            line-height: 36px;
            background: #F45757;

            span {
                float: right;
                cursor: pointer;
            }
        }

        .editBody {
            width: 100%;
            padding: 10px;
            padding-bottom: 20px;

            .item {
                float: left;
                width: 100%;
                margin-top: 5px;

                .left {
                    float: left;
                    width: 20%;
                    padding: 5px 10px;
                    text-align: right;
                    color: #F45757;
                    font-size: 14px;
                }
                .right {
                    float: left;
                    width: 76%;
                    padding: 5px;
                    text-align: justify;
                    color: #2b2b2b;
                    font-size: 14px;

                    input, textarea {
                        width: 100%;
                        padding: 5px 10px;
                        color: #2b2b2b;
