                        line-height: 24px;
                        border-radius: 3px;
                        border: 1px solid #ddd;
                        transition: border 200ms ease;

                        &:focus, &:hover {
                            border: 1px solid #F45757;
                        }
                    }

                    textarea {
                        height: 200px;
                        resize: none;
                    }
                    
                    .inputTips {
                        color: #999;
                        font-size: 12px;
                        line-height: 12px;
                    }
                }

                .replyBtn {
                    width: 100px;
                    margin: 0 auto;

                    button {
                        width: 100px;
                        height: 32px;
                        margin-top: 5px;
                        color: #aaa;
                        font-size: 14px;
                        line-height: 32px;
                        background: #F6F6F6;
                        border-radius: 3px;
                        transition: all 200ms ease;
                    }

                    .able {
                        color: #FFF;
