                        cursor: pointer;
                        user-select: none;

                        &:hover {
                            color: #F45757;
                        }

                         &:active {
                            color: #d44343;
                        }
                    }
                }   
            }
        }
    }
}

/* 必需 */
.fade-transition {
    transition: all .3s ease;
    opacity: 1;
}

/* .fade-enter 定义进入的开始状态 */
/* .fade-leave 定义离开的结束状态 */
.fade-enter, .fade-leave {
    opacity: 0;
}
</style>


